<template>
    <div class="p-1">
        <form @submit.prevent="cekTagihan">
            <div class="col-8 align-self-center mx-auto d-block mb-3 text-center">
                <img :src="$store.state.url_images+'modal/PDAM-Kotabaru.png'" class="img-fluid w-25" alt="Logo PDAM Kotabaru">
                <p class="font-weight-bold">PDAM KOTABARU</p>
            </div>
            <div class="form-group">
                <input v-model="no_meter" type="text" class="form-control" placeholder="No Meter" required>
            </div>
            <div class="form-group">
                <input v-model="nama_pelanggan" type="text" class="form-control" placeholder="Nama Pelanggan" required>
            </div>
            <button v-if="loading" class="btn btn-block tombol text-white"><i class="fa fa-spinner fa-spin"></i></button>
            <button v-else class="btn btn-block tombol text-white" type="submit">Cek Tagihan</button>
            <p class="text-muted small mt-2">* Tagihan akan dikirimkan ke nomor Whatsapp Pian</p>
        </form>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    name:'pdam',
    data(){
        return {
            loading: false,
            no_meter:"",
            nama_pelanggan:""
        }
    },
    methods:{
        cekTagihan(){
            this.loading= true;
            var request = {
                no_meter: this.no_meter,
                nama_pelanggan: this.nama_pelanggan
            }
            this.axios.post(this.$store.state.url_api + "pdam/bill-check", request, {
                headers:{
                    "Authorization": "Bearer " + localStorage.token
                },
            }).then(response => {
                if(response.data.meta.statusText == "OK"){
                    this.loading=false;
                    $('#pdamMessage').modal('show');
                }
            })
        }
    }
}
</script>